let slider2 = document.getElementById('slider-participants');
let sliderInner2 = document.getElementById('slides-participants');
prev2 = document.getElementById('prev-participants');
next2 = document.getElementById('next-participants');

slide(slider2, sliderInner2, prev2, next2);

function slide(slider, sliderInner, prev, next) {
	let posX1 = 0;
	let posX2 = 0;
	let posInitial;
	let posFinal;
	let threshold = 100;
	let slides = sliderInner.getElementsByClassName('card');
	let slidesLength = slides.length;
	let slideSize = slides[0].offsetWidth;
	let firstSlide = slides[0];
	let lastSlide = slides[slidesLength - 1];
	let cloneFirst = firstSlide.cloneNode(true);
	let cloneLast = lastSlide.cloneNode(true);

	cloneFirst.setAttribute('aria-hidden', true);
	cloneLast.setAttribute('aria-hidden', true);
	sliderInner.insertBefore(cloneFirst, null);
	sliderInner.insertBefore(cloneLast, firstSlide);
}
